import React, { PureComponent } from 'react';
import { navigate } from 'gatsby';
import { Formik, Form, ErrorMessage } from 'formik';
import { compose } from 'redux';
import { array, object } from 'yup';

import Box from 'components/Box'
import Text from 'components/Text'
import Container from 'components/Container'
import Flex from 'components/Flex'
import ButtonYes from 'components/ButtonYes';
import ButtonNo from 'components/ButtonNo';
import Button from 'components/Button';
import Image from 'components/Image';
import FullpageVerticalCenter from 'components/FullpageVerticalCenter'
import { mobileOrDesktop } from 'components/ThemeProvider/theme';

import setAnswerHoc from './setAnswerHoc'
import testicon from './test-icon.svg'
import Layout from '../Layout';
import withResponsive from '../../hoc/withResponsive'

export const questions = [
  '上一次看診時，你有明確告訴醫生所有症狀嗎？',
  '你知道你的病名嗎？',
  '上一次看診時，有告知醫生病史或過敏情形嗎？',
  '你知道每個藥分別治療的症狀及服用方式嗎？',
  '你有沒有遵從醫師的服藥指示按時吃藥？',
  '你知道你有哪些治療選擇嗎？',
  '你知道你使用的藥有什麼副作用嗎？',
  '你有遵守醫師說的注意事項嗎？(例如：不能吃冰、多運動等等）',
]


// const schema = object().shape({
//   q: array().required().min(questions.length)
// })

class index extends PureComponent {

  render() {
    const { setAnswer, isMobile } = this.props
    const content = (
      <Container py="1em">
        <Box width="5em" mx="auto">
          <Image src={testicon} />
        </Box>
        <Formik
          initialValues={{ q: [] }}
          onSubmit={({ q }) => {
            setAnswer(q)
            navigate('/test/result')
          }}
        >
          {({ handleChange, values }) => console.log(values.q) || (
            <Form>
              <Flex
                flexDirection={mobileOrDesktop('column', 'row')}
                flexWrap="wrap"
                alignItems="center"
              >
              {questions.map((q, index) => {
                return (
                <Box mt="2em" width={mobileOrDesktop(1, 1/4)} key={index}>
                  <Box
                    mx={mobileOrDesktop('1em', '0.5em')}
                    bg="white"
                    boxShadow="0px 3px 4px 4px rgba(0,0,0,0.1)"
                    borderRadius="1em">
                    <Flex
                      bg="questionBg"
                      px="2.5em"
                      borderRadius="1em 1em 0 0"
                      height={mobileOrDesktop('6em', '4.5em')}
                      alignItems="center"
                      justifyContent="center"
                    ><Text textAlign="center" lineHeight="1.4" fontSize={mobileOrDesktop('1.25em', '1em')}>{q}</Text></Flex>
                    <Flex px="0.5em" py="0.5em" mx={mobileOrDesktop('2em', '0.5em')}>
                      <ButtonYes
                        active={values.q[index]}
                        onClick={() => handleChange({
                          target: {
                            name: `q[${index}]`,
                            value: true
                          },
                          persist: () => {},
                        })}
                      />
                      <ButtonNo
                        active={values.q[index] === false}
                        onClick={() => handleChange({
                          target: {
                            name: `q[${index}]`,
                            value: false
                          },
                          persist: () => {},
                        })}
                      />
                    </Flex>
                  </Box>
                </Box>
              )})}
              </Flex>
              <Box my="2em" textAlign="center">
                <Button type="submit" px="2em">查看測驗結果</Button>
              </Box>
            </Form>
          )}
        </Formik>
      </Container>
    )
    return (
      <Layout>
        {isMobile ? (
          content
        ) : (
          <FullpageVerticalCenter>
            {content}
          </FullpageVerticalCenter>
        )}
      </Layout>
    );
  }
}

export default compose(setAnswerHoc, withResponsive)(index);
